import React, { CSSProperties, useEffect, useState } from 'react'
import {
  useMediaQuery, useTheme, Grid, Table, TableContainer, Hidden,
  TableHead, TableBody, TableRow, TableCell, Paper, Collapse, IconButton
} from '@material-ui/core'
import {
  DateField,
  Filter,
  FilterProps,
  List,
  ListProps,
  Pagination,
  TextField,
  TextInput,
  useListContext,
  useListSortContext,
  SelectInput
} from 'react-admin'
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown'
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import { useHistory } from 'react-router-dom'
import { useSelector } from 'react-redux';
import { MobilePostPagination } from '../common/Pagination'
import { DateFilterInput } from './DateFilterInput';
import { ExtendedLog } from '../../core/logs/extended-log'
import { ResourceName } from '../../core/ResourceName';
import { getContrastColor } from '../../core/common/visual.utils';
import { reducerType } from '../../core/common/theme.reducer';
import { BookingButton, CenterGrid, FullWidthButton, NoPaddingCell } from './LogsComponents';

const LogRow = ({ data }: { data: ExtendedLog }) => {
  const [open, setOpen] = useState(false)
  const [color, setColor] = useState(data.color ? data.color : "transparent")
  const history = useHistory()
  const textColor = useSelector((state: reducerType) => state.theme.darkMode === 'light' ? 'black' : 'white')

  useEffect(() => {
    if (data.color === null) {
      setColor("transparent")
    }
    else {
      setColor(data.color)
    }
    setOpen(false)
  }, [data])

  const defineStyle = () => {
    if(data.color !== null && getContrastColor(data.color) === 'white' || data.color === null && textColor === 'white')
      return ({color: 'white', fontWeight: 'bold'}) as CSSProperties
    return ({color: 'black', fontWeight: 'normal'}) as CSSProperties
  }

  return (
    <>
      <TableRow style={{ backgroundColor: color }}>
        <Hidden mdUp>
          <TableCell>
            <IconButton onClick={() => setOpen(!open)}>
              {open ?
                <ArrowDropUpIcon style={defineStyle()} /> :
                <ArrowDropDownIcon style={defineStyle()} />}
            </IconButton>
          </TableCell>
        </Hidden>
        <TableCell>
          <DateField label="Data" record={data} source="date" showTime
            style={defineStyle()} />
        </TableCell>
        <TableCell>
          <TextField label="Urzdzenie" record={data} source="deviceName"
            style={defineStyle()} />
        </TableCell>
        <TableCell>
          <TextField label="Bilet" record={data} source="ticketCode"
            style={defineStyle()} />
        </TableCell>
        <Hidden smDown>
          <TableCell>
            <TextField label="Opis" record={data} source="text" sortable={false}
              style={defineStyle()} />
          </TableCell>
        </Hidden>
        <Hidden smDown>
          <TableCell>
            {data.bookingId == null ?
              <div /> :
              <BookingButton onClick={() => history.push({ pathname: `/${ResourceName.TICKET_DETAILS}/${data.bookingId}/show` })}>
                {data.bookingId}
              </BookingButton>}
          </TableCell>
        </Hidden>
      </TableRow>
      <Hidden mdUp>
        <TableRow>
          <NoPaddingCell colSpan={4}>
            <Collapse in={open}>
              <Grid container style={{ marginTop: "1%", marginBottom: "1%" }} item>
                <CenterGrid item xs={5}>Rezerwacja:</CenterGrid>
                <CenterGrid item xs={7}>
                  {data.bookingId == null ?
                    <div /> :
                    <BookingButton onClick={() => history.push({ pathname: `/${ResourceName.TICKET_DETAILS}/${data.bookingId}/show` })}>
                      {data.bookingId}
                    </BookingButton>}
                </CenterGrid>
                <hr style={{ width: "90%", marginTop: "1%", marginBottom: "1%" }} />
                <CenterGrid item xs={5}>Opis:</CenterGrid>
                <CenterGrid item xs={7}>{data.text}</CenterGrid>
              </Grid>
            </Collapse>
          </NoPaddingCell>
        </TableRow>
      </Hidden>
    </>
  )
}

export const LogGrid = () => {
  const { ids, data } = useListContext()
  const { currentSort, setSort } = useListSortContext()

  const inverseOrder = (order: string) => (order === 'ASC' ? 'DESC' : 'ASC')

  const handleSortChange = (sortBy: string) => {
    setSort(
      sortBy,
      sortBy === currentSort.field
        ? inverseOrder(currentSort.order)
        : 'DESC'
    )
  }

  const HeaderCell = (name: string, display: string) => (
    <>
      {currentSort.field === name
        ? <FullWidthButton
          startIcon={currentSort.order === 'ASC' ? <ArrowUpwardIcon /> : <ArrowDownwardIcon />}>{display}</FullWidthButton>
        : <FullWidthButton>{display}</FullWidthButton>}
    </>
  )

  return (
    <TableContainer component={Paper}>
      <Table aria-label="a dense table" size="small" >
        <TableHead>
          <TableRow>
            <Hidden mdUp>
              <TableCell style={{ width: "3%" }} />
            </Hidden>
            <TableCell onClick={() => handleSortChange("date")} style={{ width: "10%" }}>
              {HeaderCell("date", "Data")}</TableCell>
            <TableCell onClick={() => handleSortChange("deviceName")} style={{ width: "15%" }}>
              {HeaderCell("deviceName", "Urzdzenie")}</TableCell>
            <TableCell onClick={() => handleSortChange("ticketCode")} style={{ width: "25%" }}>
              {HeaderCell("ticketCode", "Bilet")}</TableCell>
            <Hidden smDown>
              <TableCell style={{ textAlign: "center", width: "25%" }}>OPIS</TableCell>
            </Hidden>
            <Hidden smDown>
              <TableCell onClick={() => handleSortChange("bookingId")} style={{ width: "15%" }}>{HeaderCell("bookingId", "Rezerwacja")}</TableCell>
            </Hidden>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map(id =>
            <LogRow key={id} data={data[id] as ExtendedLog} />
          )}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

const ListFilters = (props: Omit<FilterProps, 'children'>) => (
  <Filter {...props}>
    <TextInput label="Kod biletu" source="ticketCode" alwaysOn />
    <SelectInput label="Urzdzenie" source="deviceName" choices={[
      { id: 'Czytnik', name: 'Czytnik' },
      { id: 'Przej\u015Bcie', name: 'Przej\u015Bcie' }]} />
    <DateFilterInput source="date" key="date" />
    <TextInput label="Rezerwacja" source="bookingId" />
    <TextInput label="Wydarzenie" source="event" />
  </Filter>
)

export const LogList = (props: ListProps) => {
  const theme = useTheme()

  return (
    <List
      perPage={20}
      exporter={false}
      pagination={useMediaQuery(theme.breakpoints.up('md')) ? <Pagination rowsPerPageOptions={[20, 50, 100]} /> : <MobilePostPagination />}
      filters={<ListFilters />}
      sort={{ field: 'date', order: 'DESC' }}
      {...props}>
      <LogGrid />
    </List>
  )
}
